import React from 'react';
import { Link } from 'react-router-dom';
// import Button from '../Button1/Button1';
import NavButton from '../NavButton/NavButton';
import './Navbar.css';
import Resume from '../../documents/Joseph S Camyre Resume.pdf';

export default function Navbar(){
    return (
        <div className='navbar-container'>
            <div className='header-menu-wrapper'>
                <nav role='navigation' class='nav-menu w-nav-menu'>
                    <ul className='header-navigation'>
                        <li className='nav-item-wrapper'>
                            {/* I think I have to make a new button component because Button class overrides nav-link */}
                            <NavButton>
                                <Link to='/' target='_blank'>Home</Link>
                            </NavButton>
                        </li>
                        <li className='nav-item-wrapper'>
                            {/* I think I have to make a new button component because Button class overrides nav-link */}
                            <NavButton>
                                <Link to='/about' target='_blank'>About</Link>
                            </NavButton>
                        </li>
                        <li className='nav-item-wrapper'>
                            {/* I think I have to make a new button component because Button class overrides nav-link */}
                            <NavButton>
                                <Link to='/portfolio' target='_blank'>Home</Link>
                            </NavButton>
                        </li>
                        <li className='nav-item-wrapper'>
                            {/* I think I have to make a new button component because Button class overrides nav-link */}
                            <NavButton>
                                <a href={Resume} target='_blank' rel='noreferrer'>Resume</a>
                            </NavButton>
                        </li>
                    </ul>
                </nav>
            </div>
        </div>
    )
}
